import React from "react";
import PropTypes from "prop-types";
import { Upload, Icon, Modal, message } from "antd";
// import {reqDeleteImg} from '../../api'
import { BASE_IMG_URL } from "../../utils/constants";
import { reqDeleteImg } from "../../api";
/*

 */
export default class PicturesWall extends React.Component {
  static propTypes = {
    imgs: PropTypes.array,
  };

  state = {
    previewVisible: false, // Modal
    previewImage: "", // url
    fileList: [
      /*{
        uid: '-1', // fileid
        name: 'xxx.png', // 
        status: 'done', // : done-, uploading: , removed: 
        url: 'https://zos.alipayobjects.com/rmsportal/jkjgkEfvpUPVyRjUImniVslZfWPnJuuZ.png', // 
      },*/
    ],
  };

  constructor(props) {
    super(props);

    let fileList = [];

    // imgs
    const { imgs } = this.props;
    if (imgs && imgs.length > 0) {
      fileList = imgs.map((img, index) => ({
        uid: -index, // fileid
        name: img, // 
        status: "done", // : done-, uploading: , removed: 
        url: BASE_IMG_URL + img,
      }));
    }

    // 
    this.state = {
      previewVisible: false, // Modal
      previewImage: "", // url
      fileList, // 
    };
  }

  /*
  
   */
  getImgs = () => {
    return this.state.fileList.map((file) => file.name);
  };

  /*
  Modal
   */
  handleCancel = () => this.setState({ previewVisible: false });

  handlePreview = (file) => {
    console.log("handlePreview()", file);
    // file
    this.setState({
      previewImage: file.url || file.thumbUrl,
      previewVisible: true,
    });
  };

  /*
  file: (/)
  fileList: 
   */
  handleChange = async ({ file, fileList }) => {
    console.log("handleChange()", file.status, fileList.length, file === fileList[fileList.length - 1]);

    // , file(name, url)
    if (file.status === "done") {
      const result = file.response; // {status: 0, data: {name: 'xxx.jpg', url: ''}}
      if (result.status === 0) {
        message.success("!");
        const { name, url } = result.data;
        file = fileList[fileList.length - 1];
        file.name = name;
        file.url = url;
      } else {
        message.error("");
      }
    } else if (file.status === "removed") {
      console.log(file);
      // 
      const result = await reqDeleteImg(file.name);
      if (result.status === 0) {
        message.success("!");
      } else {
        message.error("!");
      }
    }

    // (/)fileList

    this.setState({ fileList });
  };

  render() {
    const { previewVisible, previewImage, fileList } = this.state;
    const uploadButton = (
      <div>
        <Icon type="plus" />
        <div>Upload</div>
      </div>
    );
    return (
      <div>
        <Upload
          action="/manage/img/upload" /**/
          accept="image/*" /**/
          name="image" /**/
          listType="picture-card" /**/
          fileList={fileList} /**/
          onPreview={this.handlePreview}
          onChange={this.handleChange}>
          {fileList.length >= 4 ? null : uploadButton}
        </Upload>

        <Modal visible={previewVisible} footer={null} onCancel={this.handleCancel}>
          <img alt="example" style={{ width: "100%" }} src={previewImage} />
        </Modal>
      </div>
    );
  }
}
