import React, { Component } from "react";
import { Card, Icon, Form, Input, Cascader, Button, message } from "antd";

import PicturesWall from "./pictures-wall";
import LinkButton from "../../components/link-button";
import { reqCategorys, reqAddOrUpdateProduct } from "../../api";
import RichTextEditor from "./rich-text-editor.js";

const { Item } = Form;
const { TextArea } = Input;

class ProductAddUpdate extends Component {
  state = {
    options: [],
  };
  constructor(props) {
    super(props);
    this.pw = React.createRef();
    this.editor = React.createRef();
  }

  initOptions = async (categorys) => {
    // categorysoptions
    const options = categorys.map((c) => ({
      value: c._id,
      label: c.name,
      isLeaf: false, // 
    }));

    // 
    const { isUpdate, product } = this;
    const { pCategoryId } = product;
    if (isUpdate && pCategoryId !== "0") {
      // 
      const subCategorys = await this.getCategorys(pCategoryId);
      // options
      const childOptions = subCategorys.map((c) => ({
        value: c._id,
        label: c.name,
        isLeaf: true,
      }));

      // option
      const targetOption = options.find((option) => option.value === pCategoryId);

      // option
      targetOption.children = childOptions;
    }

    // options
    this.setState({
      options,
    });
  };

  getCategorys = async (parentId) => {
    const result = await reqCategorys(parentId);
    if (result.status === 0) {
      const categorys = result.data;
      if (parentId === "0") {
        this.initOptions(categorys);
      } else {
        return categorys;
      }
    }
  };

  validatePrice = (rule, value, callback) => {
    if (value * 1 > 0) {
      callback();
    } else {
      callback("price must be more than 0");
      // callback("0");
    }
  };

  loadData = async (selectedOptions) => {
    const targetOption = selectedOptions[0];
    targetOption.loading = true;
    const subCategorys = await this.getCategorys(targetOption.value);
    targetOption.loading = false;
    if (subCategorys && subCategorys.length > 0) {
      const childOptions = subCategorys.map((c) => ({
        value: c._id,
        label: c.name,
        isLeaf: true,
      }));
      targetOption.children = childOptions;
    } else {
      targetOption.isLeaf = true;
    }
    this.setState({
      options: [...this.state.options],
    });
  };

  submit = () => {
    this.props.form.validateFields(async (err, values) => {
      if (!err) {
        const { name, desc, price, categoryIds } = values;
        let pCategoryId, categoryId;
        if (categoryIds.length === 1) {
          pCategoryId = "0";
          categoryId = categoryIds[0];
        } else {
          pCategoryId = categoryIds[0];
          categoryId = categoryIds[1];
        }
        const imgs = this.pw.current.getImgs();
        const detail = this.editor.current.getDetail();
        console.log(detail);

        const product = { name, desc, price, imgs, detail, pCategoryId, categoryId };

        if (this.isUpdate) {
          product._id = this.product._id;
        }
        console.log(product);
        const result = await reqAddOrUpdateProduct(product);
        if (result.status === 0) {
          message.success(`${this.isUpdate ? "Update" : "Add"}product sucessfully!`);
          // message.success(`${this.isUpdate ? "" : ""}`);
        } else {
          message.success(`${this.isUpdate ? "Update" : "Add"}product failed`);
        }
      }
    });
  };

  componentDidMount() {
    this.getCategorys("0");
  }
  componentWillMount() {
    const product = this.props.location.state;
    this.isUpdate = !!product;
    this.product = product || {};
  }

  render() {
    const { isUpdate, product } = this;
    const { pCategoryId, categoryId, imgs, detail } = product;
    const categoryIds = [];
    if (isUpdate) {
      // 
      if (pCategoryId === "0") {
        categoryIds.push(categoryId);
      } else {
        // 
        categoryIds.push(pCategoryId);
        categoryIds.push(categoryId);
      }
    }
    const formItemLayout = {
      labelCol: { span: 2 }, // label
      wrapperCol: { span: 8 }, // 
    };

    const title = (
      <span>
        <LinkButton
          onClick={() => {
            this.props.history.goBack();
          }}>
          <Icon type="arrow-left" style={{ fontSize: 20 }}></Icon>
        </LinkButton>
        <span>{isUpdate ? "Modify Product" : "Add Product"}</span>
        {/* <span>{isUpdate ? "" : ""}</span> */}
      </span>
    );
    const { getFieldDecorator } = this.props.form;
    return (
      <Card title={title}>
        <Form {...formItemLayout}>
          <Item label="Name">
            {/* <Item label=""> */}
            {getFieldDecorator("name", {
              initialValue: product.name,
              rules: [{ required: true, message: "product name is required" }],
            })(<Input placeholder="Please enter product name"></Input>)}
            {/* {getFieldDecorator("name", {
              initialValue: product.name,
              rules: [{ required: true, message: "" }],
            })(<Input placeholder=""></Input>)} */}
          </Item>
          <Item label="Description">
            {/* <Item label=""> */}
            {getFieldDecorator("desc", {
              initialValue: product.desc,
              rules: [{ required: true, message: "product description is required" }],
            })(<TextArea placeholder="Please enter product description" autosize={{ minRows: 2, maxRows: 6 }} />)}
            {/* {getFieldDecorator("desc", {
              initialValue: product.desc,
              rules: [{ required: true, message: "" }],
            })(<TextArea placeholder="" autosize={{ minRows: 2, maxRows: 6 }} />)} */}
          </Item>
          <Item label="Price">
            {/* <Item label=""> */}
            {getFieldDecorator("price", {
              initialValue: product.price,
              rules: [{ required: true, message: "product price is required" }, { validator: this.validatePrice }],
            })(<Input type="number" placeholder="Please enter product price" addonAfter="Dollors" />)}
            {/* {getFieldDecorator("price", {
              initialValue: product.price,
              rules: [{ required: true, message: "" }, { validator: this.validatePrice }],
            })(<Input type="number" placeholder="" addonAfter="" />)} */}
          </Item>
          <Item label="Category">
            {/* <Item label=""> */}
            {getFieldDecorator("categoryIds", {
              initialValue: categoryIds,
              rules: [{ required: true, message: "product category is required" }],
            })(
              <Cascader placeholder="Please enter product category" options={this.state.options} /**/ loadData={this.loadData} /*, */ />
            )}
          </Item>
          <Item label="Images">
            {/* <Item label=""> */}
            <PicturesWall ref={this.pw} imgs={imgs} />
          </Item>
          <Item label="Details" labelCol={{ span: 2 }} wrapperCol={{ span: 20 }}>
            {/* <Item label="" labelCol={{ span: 2 }} wrapperCol={{ span: 20 }}> */}
            <RichTextEditor ref={this.editor} detail={detail} />
          </Item>
          <Item>
            <Button type="primary" onClick={this.submit}>
              {/*  */} Submit
            </Button>
          </Item>
        </Form>
      </Card>
    );
  }
}

export default Form.create()(ProductAddUpdate);
