import React, { Component } from "react";
import { Card, Button, Table, Modal, message } from "antd";
import { PAGE_SIZE } from "../../utils/constants";
import { reqRoles, reqAddRole, reqUpdateRole, reqDeleteRole } from "../../api";
import AddForm from "./add-form";
import AuthForm from "./auth-form";
import memoryUtils from "../../utils/memoryUtils";
import { formateDate } from "../../utils/dateUtils";
import storageUtils from "../../utils/storageUtils";
import LinkButton from "../../components/link-button";

/*

 */
export default class Role extends Component {
  state = {
    roles: [], // 
    role: {}, // role
    isShowAdd: false, // 
    isShowAuth: false, // 
  };

  constructor(props) {
    super(props);

    this.auth = React.createRef();
  }

  initColumn = () => {
    this.columns = [
      {
        title: "Role",
        // title: "",
        dataIndex: "name",
      },
      {
        title: "Create_Time",
        // title: "",
        dataIndex: "create_time",
        render: formateDate,
        // render: (create_time) => formateDate(create_time),
      },
      {
        title: "Auth_Time",
        // title: "",
        dataIndex: "auth_time",
        render: formateDate,
      },
      {
        title: "Auth_Name",
        // title: "",
        dataIndex: "auth_name",
      },
      {
        title: "Operation",
        // title: "",
        render: (role) => (
          <span>
            <LinkButton onClick={() => this.deleteRole(role)}>delete</LinkButton>
            {/* <LinkButton onClick={() => this.deleteRole(role)}></LinkButton> */}
          </span>
        ),
      },
    ];
  };

  /*
  role
   */
  deleteRole = (role) => {
    Modal.confirm({
      title: `confirm to delete ${role.name}?`,
      // title: `${role.name}?`,
      onOk: async () => {
        const result = await reqDeleteRole(role._id);
        if (result.status === 0) {
          message.success("Delete role successfully!");
          // message.success("!");
          this.getRoles();
        }
      },
    });
  };

  getRoles = async () => {
    const result = await reqRoles();
    if (result.status === 0) {
      const roles = result.data;
      this.setState({
        roles,
      });
    }
  };

  onRow = (role) => {
    return {
      onClick: (event) => {
        // 
        // console.log("row onClick()", role);
        // alert('')
        this.setState({
          role,
        });
      },
    };
  };

  /*
  
   */
  addRole = () => {
    // , 
    this.form.validateFields(async (error, values) => {
      if (!error) {
        // 
        this.setState({
          isShowAdd: false,
        });

        // 
        const { roleName } = values;
        this.form.resetFields();

        // 
        const result = await reqAddRole(roleName);
        // /
        if (result.status === 0) {
          message.success("Add role successfully");
          // message.success("");
          // this.getRoles()
          // 
          const role = result.data;
          // roles
          /*const roles = this.state.roles
          roles.push(role)
          this.setState({
            roles
          })*/

          // roles: 
          this.setState({
            roles: [...this.state.roles, role],
          });
          // this.setState((state) => ({
          //   roles: [...state.roles, role],
          // }));
          console.log("set roles");
        } else {
          message.success("Add role failed");
          // message.success("");
        }
      }
    });
  };

  /*
  
   */
  updateRole = async () => {
    // 
    this.setState({
      isShowAuth: false,
    });

    const role = this.state.role;
    // menus
    const menus = this.auth.current.getMenus();
    role.menus = menus;
    role.auth_time = Date.now();
    role.auth_name = memoryUtils.user.username;

    // 
    const result = await reqUpdateRole(role);
    if (result.status === 0) {
      // this.getRoles()
      // , 
      if (role._id === memoryUtils.user.role_id) {
        memoryUtils.user = {};
        storageUtils.removeUser();
        this.props.history.replace("/login");
        message.success("Please logout and set role");
        // message.success("");
      } else {
        message.success("Set role successfully");
        // message.success("");
        this.setState({
          roles: [...this.state.roles],
        });
      }
    }
  };

  componentWillMount() {
    this.initColumn();
  }

  componentDidMount() {
    this.getRoles();
  }

  render() {
    const { roles, role, isShowAdd, isShowAuth } = this.state;

    const title = (
      <span>
        <Button type="primary" onClick={() => this.setState({ isShowAdd: true })}>
          {/*  */}Create Role
        </Button>{" "}
        &nbsp;&nbsp;
        <Button type="primary" disabled={!role._id} onClick={() => this.setState({ isShowAuth: true })}>
          {/*  */}Role Settings
        </Button>
      </span>
    );

    return (
      <Card title={title}>
        <Table
          bordered
          rowKey="_id"
          dataSource={roles}
          columns={this.columns}
          pagination={{ defaultPageSize: PAGE_SIZE }}
          rowSelection={{
            type: "radio",
            selectedRowKeys: [role._id],
            onSelect: (role) => {
              // radio
              this.setState({
                role,
              });
            },
          }}
          onRow={this.onRow}
        />

        <Modal
          title="Add Role"
          // title=""
          visible={isShowAdd}
          onOk={this.addRole}
          onCancel={() => {
            this.setState({ isShowAdd: false });
            this.form.resetFields();
          }}>
          <AddForm setForm={(form) => (this.form = form)} />
        </Modal>

        <Modal
          title="Role Permission Settings"
          // title=""
          visible={isShowAuth}
          onOk={this.updateRole}
          onCancel={() => {
            this.setState({ isShowAuth: false });
          }}>
          <AuthForm ref={this.auth} role={role} />
        </Modal>
      </Card>
    );
  }
}
